require('./bootstrap');
Vue.component('monster_game', require('./components/Game.vue'));

new Vue({
    el: '#game-container',
    data: {
        monsterHealth: 100,
        playerHealth: 100,
        isRunGame: false,
        turns: []
    },
    methods: {
        startGame: function () {
            this.monsterHealth = 100;
            this.playerHealth = 100;
            this.turns = [];
            this.isRunGame = true;
        },
        endGame: function () {
            this.isRunGame = false;
        },
        attackPrimary: function () {
            if (this.check()) {
                this.attack(3, 10);
            }
        },
        specialAttack: function () {
            if (this.check()) {
                this.attack(10, 20);
            }
        },
        attack: function (minDamage, maxDamage) {
            if (this.check()) {
                var damage = this.calculateDamage(minDamage, maxDamage);
                this.monsterHealth -= damage;
                damage = this.calculateDamage(minDamage, maxDamage);
                this.playerHealth -= damage;
            }
        },
        heal: function () {

        },
        calculateDamage: function (min, max) {
            return Math.max(Math.floor(Math.random() * max) + 1, min);
        },
        check: function () {
            if (this.alertEndGame(this.monsterHealth, 'Fuck eah! You win!')) {
                return false;
            } else if (this.alertEndGame(this.playerHealth, 'Your failure will go down in history')) {
                return false;
            }
            return true;
        },
        alertEndGame: function (health, alert) {
            if (health <= 0) {
                alert(alert);
                return true;
            }
            return false;
        }
    }
});